// Plyr Demo
// =============================================================

const plyrDemo = {

  init () {

    this.bindUIActions()

  },

  bindUIActions () {

    // event handlers
    this.handlePlyr()
  },

  handlePlyr () {
    // Audio
    const audio = new Plyr('#audio')
    // Video
    const video = new Plyr('#video')
    // Youtube
    const youtube = new Plyr('#youtube')
    // Vimeo
    const vimeo = new Plyr('#vimeo')

    // handle plyr controls
    $(document).on('click', '.controls-video > .btn', function () {
      const fn = $(this).data('plyr')
      video[0][fn]()
    })
    // controls youtube
    .on('click', '.controls-youtube > .btn', function () {
      const fn = $(this).data('plyr')
      youtube[0][fn]()
    })
    // controls vimeo
    .on('click', '.controls-vimeo > .btn', function () {
      const fn = $(this).data('plyr')
      vimeo[0][fn]()
    })
  }
}

plyrDemo.init()
